









import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import { delay } from '@4dst-saas/public-utils/dist/delay';
import { defineComponent, inject, ref } from '@vue/composition-api';
import html2canvas from 'html2canvas';
import domtoimage from 'dom-to-image';
import exec from '@/utils/exec';
import DetailPage from './DetailPage.vue';

export default defineComponent({
  components: { DetailPage },
  setup(_, { refs, root }) {
    const isQt = inject('isQt') as () => boolean;
    const t = (s: string) => root.$t(`material-mall.${s}`);
    const name = ref('');
    const screenshotUsable = ref(false);
    const buttonTextes = ['正在加载数据中，请稍后', t('download_image_to_share'), t('image_generating')];
    const buttonText = ref(buttonTextes[1]);
    function handleLoadend(n: string) {
      console.log(n);
      screenshotUsable.value = true;
      name.value = n;
      // eslint-disable-next-line prefer-destructuring
      buttonText.value = buttonTextes[1];
    }
    async function screenshot() {
      if (!screenshotUsable.value) await root.$confirm('数据仍在加载中，确定要下载图片吗？');
      const el = (refs.detail as InstanceType<typeof DetailPage>).$el as HTMLDivElement;
      // eslint-disable-next-line prefer-destructuring
      buttonText.value = buttonTextes[2];
      screenshotUsable.value = false;
      domtoimage.toPng(el).then((dataUrl: string) => {
        if (isQt()) {
          exec('downloadStaticFile', dataUrl, `${name.value}.png`);
        } else {
          const a = document.createElement('a');
          a.href = dataUrl;
          a.download = name.value;
          a.click();
        }


        // eslint-disable-next-line prefer-destructuring
        buttonText.value = buttonTextes[1];
        screenshotUsable.value = true;
      });
    }
    return {
      screenshot,
      screenshotUsable,
      name,
      handleLoadend,
      buttonText,
    };
  },
});
